::selection {
    background: $highlight;
    /* WebKit/Blink Browsers */
}

::-moz-selection {
    background: $highlight;
    /* Gecko Browsers */
}

// Body

body {
  background: $background-color;
  color: $black;
  font-smooth: never;
-webkit-font-smoothing : none;
}
